.App{
 background-color:  rgb(244, 227, 227);
 height: 100vh;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.react-chatbot-kit-chat-inner-container {
  height: 81vh;
  background-color: #fff;
  border-radius: 3px;
  border-radius: 5px;
  width: 65vw;
}

.react-chatbot-kit-chat-bot-message-container {
  display: block;
}

.react-chatbot-kit-chat-bot-message {
  background-color: #F9EFEF;
  padding: 0px 10px;
  border: 0.5px solid black;
  color: black;
  width: 50%;
  margin-left: 35px;
  border-radius: 5px;
}

.react-chatbot-kit-user-chat-message{
  background-color: #F9EFEF;
    padding: 0px 10px;
    border-radius: 5px;
    color: black;
    border: 0.5px solid black;
    width: auto;
}

.react-chatbot-kit-chat-bot-message-arrow {
  display: none;
}

.react-chatbot-kit-chat-btn-send {
  background-color: #CA9A9A;
  border-radius: 10px;
  width: 100px;
  border: none;
}

.bot {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 2px solid rgba(99, 97, 97, 0.587);
  filter: blur(0.6px);
}

.user {
  background-color: #CA9A9A;
  width: 35px;
  height: 35px;
  display: grid;
  place-items: center;
  color: white;
  border-radius: 100%;
}

.react-chatbot-kit-user-chat-message {
  margin-right: 10px;
  background-color: #F9EFEF;
  color: black;
  padding: 0px 10px;
  border: 0.5px solid black;
  width: auto;
}

.react-chatbot-kit-user-chat-message-arrow {
  display: none;
}

.react-chatbot-kit-chat-container {
  height: auto;
  width: auto;
}

.react-chatbot-kit-chat-message-container {
  padding: 0 17.5px 10px 17.5px;
  height: 77.6vh;
  background-color: #F9EFEF;
}

.react-chatbot-kit-chat-input-container {
  position: relative;
  padding: 5px 10px;
  background-color: #D9D9D9;
}

.react-chatbot-kit-chat-input{
  width: 100%;
  padding: 12.5px;
  border: 1px solid black;
  background-color: #FFFFFF;
  border-radius: 10px;
}

.react-chatbot-kit-user-chat-message-container {
  display: flex;
  margin: 15px 0;
  justify-content: flex-end;
}

